@media screen and (min-width:1109px)  {
    .TableHolderPost{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .TableHolderPost table{
        margin-top: 5rem;
        border-collapse: collapse;
        min-width: 40rem;
        overflow: hidden;
        background-color: transparent;
        border-radius: 10px;
        color: white;
    }
    .TableHolderPost tr{
        border-bottom: none;
    }
    .TableHolderPost tr:active{
        background-color: transparent;
    }
    .TableHolderPost th{
        position: sticky;
        top: 0;
        background-color: rgb(11, 86, 109);
    }
    .TableHolderPost td, th{
        text-align: right;
        height: 2rem;
        cursor: default;
    }
    .TableHolderPost input{
        height: 2rem;
        margin: 1rem;
        outline: none;
        background-color: transparent;
        border: 1px solid white;
        border-radius: 5px;
        color: white;
        font-size: 1rem;
    }
    .TableHolderPost button{
        height: 3rem;
        width: 6rem;
        background-color: rgb(216, 97, 117);
        border: 1px solid black;
        border-radius: 5px;
        font-weight: bold;
        cursor: pointer;
        font-size: 1.1rem;
        margin-top: 3rem;
    }
    .TableHolderPost button:active{
        background-color: rgb(194, 55, 78);
        transform: scale(0.95);
    }
}

@media screen and (max-width:1110px)  {
    .TableHolderPost{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .TableHolderPost table{
        margin-top: 5rem;
        border-collapse: collapse;
        min-width: 20rem;
        overflow: hidden;
        background-color: transparent;
        border-radius: 10px;
        color: white;
    }
    .TableHolderPost tr{
        border-bottom: none;
    }
    .TableHolderPost tr:active{
        background-color: transparent;
    }
    .TableHolderPost th{
        position: sticky;
        top: 0;
        background-color: rgb(11, 86, 109);
    }
    .TableHolderPost td, th{
        text-align: right;
        height: 2rem;
        cursor: default;
    }
    .TableHolderPost input{
        height: 2rem;
        margin: 1rem;
        outline: none;
        background-color: transparent;
        border: 1px solid white;
        border-radius: 5px;
        color: white;
        font-size: 1rem;
    }
    .TableHolderPost button{
        height: 3rem;
        width: 6rem;
        background-color: rgb(216, 97, 117);
        border: 1px solid black;
        border-radius: 5px;
        font-weight: bold;
        cursor: pointer;
        font-size: 1.1rem;
        margin-top: 3rem;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }
    .TableHolderPost button:active{
        background-color: rgb(194, 55, 78);
        transform: scale(0.95);
    }
}