@media screen and (min-width:1109px)  {
    .Header{
        position: sticky;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        height: 4rem;
        width: 100vw;
        background-color: rgb(46, 164, 201);
    }
    .show_tab, .post_tab{
        height: 2rem;
        width: 7rem;
        background-color: transparent;
        border: 2px solid rgb(7, 97, 124);
        border-radius: 7px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.1rem;
        font-weight: bold;
        color: white;
        margin-left: 2rem;
        cursor: pointer;
    }
    .show_tab.active, .post_tab.active{
        background-color: rgb(6, 117, 151);
    }
}

@media screen and (max-width:1110px)  {
    .Header{
        position: sticky;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        height: 4rem;
        width: 100vw;
        font-weight: bold;
        background-color: rgb(46, 164, 201);
        justify-content: space-between;
    }
    .show_tab, .post_tab{
        flex: 1;
        background-color: rgb(6, 117, 151);
        height: 100%;
        color: white;
        font-size: 1.1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 3px solid transparent;
        cursor: pointer;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }
    .show_tab{
        border-right: 1px solid rgb(11, 86, 109);
    }
    .show_tab.active, .post_tab.active{
        
        border-bottom: 3px solid white;
    }
}