@media screen and (min-width:1109px)  {
    .TableHolder{
        display: flex;
        justify-content: center;
    }
    table{
        margin-top: 5rem;
        border-collapse: collapse;
        min-width: 70%;
        overflow: hidden;
        background-color: rgb(11, 89, 112);
        border-radius: 10px;
        color: white;
    }
    tr{
        border-bottom: 1px solid rgb(15, 121, 153);
    }
    tr:active{
        background-color: rgb(19, 117, 146);
    }
    th{
        position: sticky;
        top: 0;
        background-color: rgb(11, 86, 109);
    }
    td, th{
        text-align: left;
        height: 2rem;
    }
    td{
        cursor: pointer;
    }
}

@media screen and (max-width:1110px)  {
    .TableHolder{
        display: flex;
        justify-content: center;
    }
    table{
        margin-top: 3rem;
        border-collapse: collapse;
        min-width: 97%;
        overflow: hidden;
        background-color: rgb(11, 89, 112);
        border-radius: 7px;
        color: white;
        user-select: none;
    }
    th{
        position: sticky;
        top: 0;
    }
    tr:active{
        background-color: rgb(19, 117, 146);
    }
    td, th{
        text-align: left;
        height: 2rem;
    }
    p{
        max-width: 4rem;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
}